import React, {Component} from "react";
import {Helmet} from "react-helmet";
import screenshot from "../image/webpage_screenshot.png";

export default class MetaTags extends Component {
    render() {
        let companyName = "LEE Wai Shun, Wilson";
        if(window.location.pathname.toString() === "/st_news"){
            companyName = "研AI無人機搜救系統 IVE畢業生團隊奪獎 @ 星島日報 - " + companyName;
        }else if(window.location.pathname.toString() === "/bas_news"){
            companyName = "IVE畢業生研發無人機搜救系統奪獎 以AI加快救人速度 @ 巴士的報 - " + companyName;
        }else if(window.location.pathname.toString() === "/vtc_news"){
            companyName = "IVE畢業生以人工智能技術研發「無人機搜救系統」奪獎 @ VTC文摘 - " + companyName;
        }
        return (
            <React.Fragment>
                <Helmet>
                    <meta charSet="utf-8"/>
                    <title>{companyName}</title>
                    <link rel="canonical" href="https://wilsonvolker.github.io/"/>
                    <meta name="description" content="Wilson is a passionate developer who had been recognised by different companies and competitions.
                    He provides various services such as developing websites and mobile applications, and he is always looking for a challenging role in a reputable organisation to utilise his skills as well as enhancing his knowledge about new and emerging trends in IT.
            "
                    />
                    <meta property="og:locale" content="en_HK"/>
                    <meta property="og:locale:alternate" content="zh_HK"/>
                    <meta property="og:locale:alternate" content="en_US"/>
                    <meta property="og:locale:alternate" content="en_AU"/>
                    <meta property="og:locale:alternate" content="en_CA"/>
                    <meta property="og:type" content="website"/>
                    <meta property="og:title"
                          content={{companyName}}/>
                    <meta property="og:description" content="Wilson is a passionate developer who had been recognised by different companies and competitions. 
                    He provides various services such as developing websites and mobile applications, and he is always looking for a challenging role in a reputable organisation to utilise his skills as well as enhancing his knowledge about new and emerging trends in IT.
            "/>
                    <meta property="og:url" content="https://wilsonvolker.github.io/"/>
                    <meta property="og:site_name"
                          content={{companyName}}/>
                    <meta property="og:image" content={screenshot}/>
                    <meta property="og:image:width" content="1264"/>
                    <meta property="og:image:height" content="846"/>
                    <meta name="twitter:card" content="summary_large_image"/>
                    <meta name="twitter:description" content="Wilson is a passionate developer who had been recognised by different companies and competitions. 
                    He provides various services such as developing websites and mobile applications, and he is always looking for a challenging role in a reputable organisation to utilise his skills as well as enhancing his knowledge about new and emerging trends in IT."/>
                    <meta name="twitter:title"
                          content={{companyName}}/>
                    <meta name="twitter:image" content={screenshot}/>
                </Helmet>
            </React.Fragment>
        );
    }
}