import React, {Component} from 'react';
import singTaoLogo from "../image/singTaoDailyLogo.jpg";
import skyEyeNewsImage1 from "../image/skyEyeNewsImage1.jpg";
import skyEyeNewsImage2 from "../image/skyEyeNewsImage2.jpg"
import skyEyeBasImage from "../image/SkyEyeBasNews.jpg";
import skyEyeNewsImage3 from "../image/skyEyeNewsImage3.jpg";
import basNewsLogo from "../image/bastilleLogo.jpg";

class BasNews extends Component {
    render() {
        return (
            <div>
                <div className="main">
                    <div className="section text-center">
                        <div className="container">
                            <img src={basNewsLogo} alt="Bastille Logo" className="imageLogo-s" />
                            <div className="row">
                                <div className="col-md-8 ml-auto mr-auto"  id="about-me">
                                    <h2 className="title">IVE畢業生研發無人機搜救系統奪獎 以AI加快救人速度</h2>
                                    <br />
                                    <br />
                                    <img src={skyEyeNewsImage2} alt="news image" className="imageLogo-l"/><br />
                                    <i>無人機可靈活到達並拍攝一些陡峭的位置，有效提高搜救效率，減低搜救人員受傷的機會。</i>
                                    <br />
                                    <br />
                                    <h5 className="description fadeInLeft animated">
                                        無人機可靈活到達及拍攝陡峭位置，減低搜救人員受傷機會。
                                        <br/>
                                        <br/>
                                        遠足越來越受港人歡迎，但當中有不少人是新手，行山裝備不足，隨時易生意外。政府統計數字顯示，本港每年接獲的山嶺搜救召喚宗數過千宗，傷亡數字約有200人。剛畢業的IVE數據科學及分析高級文憑畢業生李瑋舜，早前和3位應屆畢業同學，留意到山嶺搜救工作往往花費大量人力物力，遂啟發他們著手研究如何運用大數據和人工智能技術配合無人機進行搜索工作，研發出「無人機搜救系統」，更於「泛珠三角+大學生計算機作品賽」中的高職組勇奪一等獎。
                                        <br />
                                        <br />
                                        李瑋舜說︰「現時消防人員進行山嶺搜救工作時，很多時會派出無人機進行大範圍高空拍攝，其後將影像連接電腦，再以肉眼掃視影像，以找出救人線索，比較費時費力。」陳梓禧接著解釋︰「搜救人員難以步至山嶺中陡峭的位置，但這些位置卻往往可能是行山人士的高危地點。無人機的優勢是可以靈活到達並拍攝現場情況，尤其是一些陡峭的位置。於是我們研究以無人機拍攝實時影像，配合人工智能技術辨識人體影像，再以手機應用程式，透過螢幕顯示求助者的座標位置，加快搜救速度。」
                                        <br/>
                                        <br/>
                                        花了近一年時間研發，經過多次試驗，隊員憑「無人機搜救系統」出戰早前舉行的「2019泛珠三角+大學生計算機作品賽」，並在高職組中勇奪一等獎。負責帶隊的IVE資訊科技學科署理高級講師高堅華指，比賽讓參賽者以趣味方式實踐所學，過程中要逐步解決難題﹕「研發過程中曾發生『墜機意外』。當時同學們在校園內測試系統，結果無人機被樹枝卡住，掉到一個斜坡的草叢中，於是啟發同學在系統中加裝自動路徑規劃及高度控制裝置，令無人機可避開大樹等大型障礙物。」
                                        <br/>
                                        <br/>

                                        李瑋舜於本學年升讀城大理學士（電腦科學）課程，陳梓禧則升讀理大數據科學及分析(榮譽)理學士學位課程，二人希望繼續進修，裝備自己，冀日後能透過大數據和人工智能的專業知識研發不同項目，回饋社會。
                                    </h5>
                                    <img src={skyEyeNewsImage1} alt="news image" className="imageLogo-l"/><br />
                                    <i>由IVE數據科學及分析高級文憑應屆畢業生簡羨康(左三)、黃紹岐(左四)、李瑋舜(右三)及陳梓禧(右二)組成的團隊花了一年時間，在IVE資訊科技學科署理高級講師高堅華(左一)及其他導師帶領下，合力利用人工智能及數據分析方法，研發出「無人機搜救系統」。</i>
                                    <br/>
                                    <br/>
                                    <img src={skyEyeNewsImage3} alt="news image" className="imageLogo-l"/> <br />
                                    <i>無人機搜救系統運用人工智能技術識辨人體影像，搜救人員可以透過手機應用程式，實時得悉失蹤者座標。</i>
                                    <br/>
                                    <br/>
                                    Source: <a target="_blank" style={{"overflowWrap": "break-word"}} href="https://www.bastillepost.com/macau/article/2490694-ive%E7%95%A2%E6%A5%AD%E7%94%9F%E7%A0%94%E7%99%BC%E7%84%A1%E4%BA%BA%E6%A9%9F%E6%90%9C%E6%95%91%E7%B3%BB%E7%B5%B1%E5%A5%AA%E7%8D%8E-%E4%BB%A5ai%E5%8A%A0%E5%BF%AB%E6%95%91%E4%BA%BA%E9%80%9F%E5%BA%A6">https://www.bastillepost.com/macau/article/2490694-ive畢業生研發無人機搜救系統奪獎-以ai加快救人速度</a>
                                </div>
                            </div>


                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

export default BasNews;