import React, {Component} from "react";
import FadeInSection from "./FadeInSection";
import Typist from "react-typist";
import TypistOnScroll from "./TypistOnScroll";
import lovingKidsLogo from "../image/lovingkids_logo.jpg";
import chuangXingLogo from "../image/atman fish banner.jpg";
import kongsLogo from "../image/kongs_logo.jpg";
import kcLogo from "../image/KwoonChungBusHoldings_logo.jpg";
import singTaoLogo from "../image/singTaoDailyLogo.jpg";
import bastilleLogo from "../image/bastilleLogo.jpg";
import vtcLogo from "../image/vtcLogo.jpg";
import {
    Link
} from "react-router-dom";

export default class AboutMe extends Component {
    render() {
        return <React.Fragment>
            <div className="main">
                <div className="section text-center">
                    <div className="container">
                        <div className="row">
                            <div className="col-md-8 ml-auto mr-auto"  id="about-me">
                                <TypistOnScroll>
                                    <h2 className="title">Hi! I am Wilson.</h2>
                                </TypistOnScroll>
                                <h5 className="description fadeInLeft animated text-bold">
                                    <FadeInSection>
                                        I like to design different kind of applications, including but not limited to web apps, mobile apps and management system for business use. I had worked with companies and won some academic achievements in recent years.
                                    </FadeInSection>
                                </h5>

                                <br/>
                            </div>
                        </div>
                        <br/>
                        <br/>

                        {/*<a >*/}
                        {/*    <button type="button" className="btn btn-success downloadCatalogBtn">Download Catalogue</button>*/}
                        {/*</a>*/}

                        <h2 className="title">Skills</h2>
                        <FadeInSection>
                            <table className="table table-hover table-bordered" >
                                <thead>
                                <tr>
                                    <th scope="col">Aspect</th>
                                    <th scope="col">Details</th>
                                </tr>
                                </thead>
                                <tbody>
                                <tr>
                                    <td>Website Development</td>
                                    <td>HTML, JavaScript, CSS, React JS, PHP, Bootstrap, JQuery and MVC</td>
                                </tr>
                                <tr>
                                    <td>Android Development</td>
                                    <td>JAVA</td>
                                </tr>
                                <tr>
                                    <td>Data Management and Manipulation</td>
                                    <td>MySQL, MSSQL, Oracle Database (SQL), and MongoDB (NoSQL)</td>
                                </tr>
                                <tr>
                                    <td>Data Science</td>
                                    <td>Python (package: tensorflow, sklearn, keras, etc.), R</td>
                                </tr>
                                </tbody>
                            </table>
                        </FadeInSection>

                        <h2 className="title">Recent Cooperation</h2>
                        <FadeInSection>
                            <div className="row">
                                <div className="col-md-3">
                                    <div className="card">
                                        <div className="card-body" >
                                            <h4 className="card-title">
                                                <a target="_blank">
                                                    <img src={lovingKidsLogo} alt="Loving Kids Logo" style={{
                                                        "height": "110px",
                                                        "width": "110px",
                                                    }}/>
                                                </a>
                                            </h4>
                                            <p className="card-text" style={{
                                                "paddingTop": "15px",
                                                "paddingBottom": "0px",
                                            }}>
                                                P.T. Analyst Programmer @ Loving Kids Community Service Limited
                                                <br/>
                                            </p>
                                        </div>
                                    </div>
                                </div>

                                <div className="col-md-3">
                                    <div className="card">
                                        <div className="card-body" onClick={() => {window.open("http://chuangxing.com.hk/")}}>
                                            <h4 className="card-title">
                                                <a href={chuangXingLogo} target="_blank">
                                                    <img src={chuangXingLogo} alt="ChuangXingLogo" style={{
                                                        "height": "110px",
                                                        "width": "110px",
                                                    }}/>
                                                </a>
                                            </h4>
                                            <p className="card-text" style={{
                                                "paddingTop": "15px",
                                                "paddingBottom": "0px",
                                            }}>
                                                Freelance Programmer @ Chuang Xing Manufacturing Co. Ltd.
                                            </p>
                                        </div>
                                    </div>
                                </div>

                                <div className="col-md-3">
                                    <div className="card">
                                        <div className="card-body" onClick={() => {window.open("http://kongs.com.hk/")}}>
                                            <h4 className="card-title">
                                                <a href={kongsLogo} target="_blank">
                                                    <img src={kongsLogo} alt="Kongs Logo" style={{
                                                        "height": "110px",
                                                        "width": "150px",
                                                    }}/>
                                                </a>
                                            </h4>
                                            <p className="card-text" style={{
                                                "paddingTop": "15px",
                                                "paddingBottom": "0px",
                                            }}>
                                                {/*<a href={birdCageCatalogue} target="_blank">*/}
                                                {/*    Download Bird (& Pets) Cage Product Catalogue*/}
                                                {/*</a>*/}
                                                Freelance Programmer @ Kong's International Co., Ltd
                                            </p>
                                        </div>
                                    </div>
                                </div>

                                <div className="col-md-3">
                                    <div className="card">
                                        <div className="card-body" >
                                            <h4 className="card-title">
                                                <a  target="_blank">
                                                    <img src={kcLogo} alt="Kwoon Chung Logo" style={{
                                                        "height": "110px",
                                                        "width": "110px",
                                                    }}/>
                                                </a>
                                            </h4>
                                            <p className="card-text" style={{
                                                "paddingTop": "15px",
                                                "paddingBottom": "0px",
                                            }}>
                                                {/*<a href={birdCageCatalogue} target="_blank">*/}
                                                {/*    Download Bird (& Pets) Cage Product Catalogue*/}
                                                {/*</a>*/}
                                                I.T. Analyst @ Kwoon Chung Motors Co.Ltd
                                            </p>
                                        </div>
                                    </div>
                                </div>

                            </div>
                        </FadeInSection>

                        <h2 className="title">Education Levels</h2>
                        <FadeInSection>
                            <table className="table table-hover table-bordered" >
                                <thead>
                                <tr>
                                    <th scope="col">School</th>
                                    <th scope="col">Subject/Major</th>
                                    <th scope="col">Grad. Status</th>
                                </tr>
                                </thead>
                                <tbody>
                                <tr>
                                    <td>BSc Computer Science</td>
                                    <td>City University of Hong Kong</td>
                                    <td>Studying</td>
                                </tr>
                                <tr>
                                    <td>Higher Diploma in Data Science and Analytics</td>
                                    <td>Hong Kong Institute of Vocational Education</td>
                                    <td>Graduated with CGPA 3.87</td>
                                </tr>
                                </tbody>
                            </table>
                        </FadeInSection>

                        <h2 className="title">Achievements</h2>
                        <FadeInSection>
                            <table className="table table-hover table-bordered" >
                                <thead>
                                <tr>
                                    <th scope="col">Organization</th>
                                    <th scope="col">Achievement</th>
                                </tr>
                                </thead>
                                <tbody>
                                <tr>
                                    <td>Tipdm – National Data Science Competition</td>
                                    <td>First Prize</td>
                                </tr>
                                <tr>
                                    <td>Pan-Pearl River Delta - Tertiary Student Computer Project Competition</td>
                                    <td>First Prize</td>
                                </tr>
                                </tbody>
                            </table>
                        </FadeInSection>

                        <h2 className="title">News About Me</h2>
                        <div className="card">
                            <div className="row no-gutters bg-light position-relative">
                                <div className="col-md-6 mb-md-0 p-md-4">
                                    <img src={singTaoLogo} className="w-100" alt="Sing Tao Daily Logo" />
                                </div>
                                <div className="col-md-6 position-static p-4 pl-md-0">
                                    <h5 className="mt-0">研AI無人機搜救系統 IVE畢業生團隊奪獎</h5>
                                    <p>「無人機搜救系統」由剛於IVE數據科學及分析高級文憑畢業、現就讀城市大學理學士（電腦科學）課程的李瑋舜，聯同三名應屆畢業同學共同研發。有別於現時消防人員派出無人機進行大範圍高空拍攝，以肉眼掃視影像，新技術利用大數據與人工智能技術，自動辨識人體影像，再以手機應用程式，通過螢幕顯示求助者的座標位置，加快搜救速度。經過多次試驗，團隊憑「無人機搜救系統」出戰「2019泛珠三角+大學生計算機作品賽」，勇奪高職組一等獎。</p>
                                    {/*<a href="#" className="stretched-link">Read more</a>*/}
                                    <Link to="/st_news" target="_blank">Read more</Link>
                                </div>
                            </div>
                        </div>
                        <div className="card">
                            <div className="row no-gutters bg-light position-relative">
                                <div className="col-md-6 mb-md-0 p-md-4">

                                    <div>
                                        <img src={bastilleLogo} className="w-100" alt="Bastille Logo" style={{
                                            "marginTop": "13%",
                                        }}/>
                                    </div>
                                </div>
                                <div className="col-md-6 position-static p-4 pl-md-0">
                                    <h5 className="mt-0">IVE畢業生研發無人機搜救系統奪獎 以AI加快救人速度</h5>
                                    <p>無人機可靈活到達及拍攝陡峭位置，減低搜救人員受傷機會。 遠足越來越受港人歡迎，但當中有不少人是新手，行山裝備不足，隨時易生意外。遠足越來越受港人歡迎，但當中有不少人是新手，行山裝備不足，隨時易生意外。政府統計數字顯示，本港每年接獲的山嶺搜救召喚宗數過千宗，傷亡數字約有200人。剛畢業的IVE數據科學及分析高級文憑畢業生李瑋舜，早前和3位應屆畢業同學，留意到山嶺搜救工作往往花費大量人力物力，遂啟發他們著手研究如何運用大數據和人工智能技術配合無人機進行搜索工作，研發出「無人機搜救系統」，更於「泛珠三角+大學生計算機作品賽」中的高職組勇奪一等獎。</p>
                                    {/*<a href="#" className="stretched-link">Read more</a>*/}
                                    <Link to="/bas_news" target="_blank">Read more</Link>
                                </div>
                            </div>
                        </div>
                        <div className="card">
                            <div className="row no-gutters bg-light position-relative">
                                <div className="col-md-6 mb-md-0 p-md-4">

                                    <div>
                                        <img src={vtcLogo} className="w-50" alt="VTC Logo" style={{
                                            // "margin-top": "13%",
                                        }}/>
                                    </div>
                                </div>
                                <div className="col-md-6 position-static p-4 pl-md-0">
                                    <h5 className="mt-0">IVE畢業生以人工智能技術研發「無人機搜救系統」奪獎</h5>
                                    <p>港人越來越注重健康，不少運動如遠足等亦漸受歡迎。惟根據政府統計數字，本港每年接獲的山嶺搜救召喚宗數過千宗，傷亡數字約有200人。香港專業教育學院(IVE)數據科學及分析高級文憑應屆畢業生有感搜救工作需要大量人力物力，故運用大數據和人工智能技術，研發出「無人機搜救系統」，更於「泛珠三角+大學生計算機作品賽」中的高職組勇奪一等獎。
                                        剛畢業的IVE數據科學及分析高級文憑畢業生李瑋舜，早前和3位應屆畢業同學，留意到山嶺搜救工作往往花費不少人力物力，遂啟發他們著手研究如何以科技貢獻社會。李瑋舜說︰「現時消防人員進行山嶺搜救工作時，很多時會派出無人機進行大範圍高空拍攝，其後將影像連接電腦，再以肉眼掃視影像，以找出救人線索，比較費時費力。」</p>
                                    {/*<a href="#" className="stretched-link">Read more</a>*/}
                                    <Link to="/vtc_news" target="_blank">Read more</Link>
                                </div>
                            </div>
                        </div>

                    </div>
                </div>
            </div>
        </React.Fragment>
    }
}