import React, {Component} from 'react';
import singTaoLogo from "../image/singTaoDailyLogo.jpg";
import skyEyeNewsImage1 from "../image/skyEyeNewsImage1.jpg";

class StNews extends Component {
    render() {
        return (
            <div>
                <div className="main">
                    <div className="section text-center">
                        <div className="container">
                            <img src={singTaoLogo} alt="Sing Tao Logo" className="imageLogo-s" />
                            <div className="row">
                                <div className="col-md-8 ml-auto mr-auto"  id="about-me">
                                    <h2 className="title">IVE畢業生研發無人機搜救系統奪獎 以AI加快救人速度</h2>
                                    <br />
                                    <img src={skyEyeNewsImage1} alt="news image" className="imageLogo-l"/>
                                    <br/>
                                    <i>1/1 李瑋舜（右三）與陳梓禧（右二）等研發的「無人機搜救系統」，在「泛珠三角+大學生計算機作品賽」奪獎。</i>
                                    <br />
                                    <br />
                                    <h5 className="description fadeInLeft animated">
                                        (星島日報報導)行山客迷路失蹤意外瀕生，山嶺搜救往往耗費大量人力物力，專業教育學院（IVE）數據科學及分析高級文憑課程四名應屆畢業生，運用大數據和人工智能技術，研發「無人機搜救系統」，更在「泛珠三角+大學生計算機作品賽」奪得高職組一等獎。
                                        <br />
                                        <br />
                                        「無人機搜救系統」由剛於IVE數據科學及分析高級文憑畢業、現就讀城市大學理學士（電腦科學）課程的李瑋舜，聯同三名應屆畢業同學共同研發。有別於現時消防人員派出無人機進行大範圍高空拍攝，以肉眼掃視影像，新技術利用大數據與人工智能技術，自動辨識人體影像，再以手機應用程式，通過螢幕顯示求助者的座標位置，加快搜救速度。經過多次試驗，團隊憑「無人機搜救系統」出戰「2019泛珠三角+大學生計算機作品賽」，勇奪高職組一等獎。
                                        <br />
                                        <br />
                                        隊員之一、現就讀理工大學數據科學及分析（榮譽）理學士學位課程的陳梓禧指，無人機優勢是可以靈活到達並拍攝現場情況，尤其陡峭的位置，搜救人員往往難以前往，卻是行山人士遇險的高危地點。指導他們的IVE資訊科技學科署理高級講師高堅華指，研發項目花了一年時間，學生測試系統曾意外墜機，啟發他們加裝自動路徑規劃及高度控制裝置，令無人機能避開障礙物。
                                    </h5>

                                    <br/>
                                    <br />
                                    Source: <a target="_blank" style={{"overflowWrap": "break-word"}} href="https://std.stheadline.com/daily/article/detail/2111766/%E6%97%A5%E5%A0%B1-%E6%95%99%E8%82%B2-%E7%A0%94AI%E7%84%A1%E4%BA%BA%E6%A9%9F%E6%90%9C%E6%95%91%E7%B3%BB%E7%B5%B1-IVE%E7%95%A2%E6%A5%AD%E7%94%9F%E5%9C%98%E9%9A%8A%E5%A5%AA%E7%8D%8E">https://std.stheadline.com/daily/article/detail/2111766/%E6%97%A5%E5%A0%B1-%E6%95%99%E8%82%B2-%E7%A0%94AI%E7%84%A1%E4%BA%BA%E6%A9%9F%E6%90%9C%E6%95%91%E7%B3%BB%E7%B5%B1-IVE%E7%95%A2%E6%A5%AD%E7%94%9F%E5%9C%98%E9%9A%8A%E5%A5%AA%E7%8D%8E</a>
                                </div>
                            </div>


                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

export default StNews;