    onSubmitBtnClicked = function(){
        let formSubmitUrl = "https://script.google.com/macros/s/AKfycbwqEQ1FX1yNmA4U0tOVxEO-Ju5JzjWccD_kPenU7Q/exec";
        let data = {
            "name": document.getElementById("name").value,
            "message": document.getElementById("message").value,
            "email": document.getElementById("email").value,
            "formDataNameOrder":  JSON.stringify(["name","message","email"]),
            "formGoogleSheetName": 'responses',
            "formGoogleSendEmail": " example@email.net",
        };

        $.post(formSubmitUrl, data);
        notify.show("Your message has been successfully received, I will reply it very soon. Thank you!", "success");
        document.getElementById("name").value = "";
        document.getElementById("message").value = "";
        document.getElementById("email").value = "";
    };

    onKeyPressedInput = function(event){
        if(event.key == 'Enter'){
            this.onSubmitBtnClicked();
        }
    };

    constructor(props) {
        super(props);
        this.onSubmitBtnClicked = this.onSubmitBtnClicked.bind(this);
        this.onKeyPressedInput = this.onKeyPressedInput.bind(this);
    }

    render() {
        return (
            <div>
                <h2 className="title text-center">Contact Me</h2>
                <div className="main">
                    <div className="section ">
                        <div className="container">
                            <form className="gform pure-form pure-form-stacked" method="POST" data-email="example@email.net">

                                <div className="form-group">
                                    <label htmlFor="name">Name</label>
                                    <input type="text" className="form-control" id="name" name="name"
                                           placeholder="Your Name" onKeyUp={this.onKeyPressedInput}/>
                                </div>

                                <div className="form-group">
                                    <label htmlFor="message">Your Message</label>
                                    <textarea className="form-control" id="message" name="message" rows="3" onKeyUp={this.onKeyPressedInput}></textarea>
                                </div>

                                <div className="form-group">
                                    <label htmlFor="email">Your Email address</label>
                                    <input type="email" className="form-control" id="email" name="email"
                                           placeholder="name@example.com" onKeyUp={this.onKeyPressedInput} />
                                </div>

                                <div className="text-center">
                                    <button type="button" onClick={this.onSubmitBtnClicked} className="btn btn-primary mb-2">Submit</button>
                                </div>
                            </form>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
