import React, {Component} from "react";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import {faLinkedin, faGithubSquare} from "@fortawesome/free-brands-svg-icons";

export default class PageFooter extends Component{
    getCurrentYear(){
        return new Date().getFullYear()
    }

    render() {
        return <footer style={{
            "color": "#9A9A9A",
        }}>
            <div className="section text-center">
                <div className="container">
                    <div className="row">
                        <div className="col-md-3">
                            <a href="https://www.linkedin.com/in/wai-shun-lee-710b15169/" target="_blank" style={{color: "#9E9E9E"}}>
                                <FontAwesomeIcon icon={faLinkedin} size="2x"/>
                            </a>
                            &nbsp;&nbsp;
                            <a href="https://github.com/wilsonvolker" target="_blank" style={{color: "#9E9E9E"}}>
                                <FontAwesomeIcon icon={faGithubSquare} size="2x"/>
                            </a>
                        </div>
                        <div className="col" style={{
                            "textAlign": "right",
                        }}>
                            Copyright © {this.getCurrentYear()} LEE Wai Shun. All Rights Reserved.
                        </div>
                    </div>
                </div>
            </div>
        </footer>
    }
}